<template>
  <div class="tj-hero">
    <!-- 슬라이드 -->
    <vue-glide
      v-model="glideIndex"
      :type="glideConfig.type"
      :startAt="glideConfig.startAt"
      :rewind="glideConfig.rewind"
      :autoplay="glideConfig.autoplay"
      :perView="glideConfig.perView"
      :gap="glideConfig.gap"
    >
      <vue-glide-slide v-for="(hero, index) in heroes" :key="index">
        <div class="tj-hero-item" :class="hero.className" :style="{ backgroundImage: 'url(' + hero.image + ')', backgroundColor: hero.backgroundColor }">
          <div class="tj-hero-item__wrapper">
            <div v-if="hero.subtitle" class="tj-hero-item__subtitle">{{ hero.subtitle }}</div>
            <h1 v-if="hero.title" class="tj-hero-item__title">{{ hero.title }}</h1>
            <div v-if="hero.buttonText">
              <TjButton :to="hero.to" :href="hero.href" :disabled="hero.disabled">{{ hero.buttonText }}</TjButton>
            </div>
          </div>
        </div>
      </vue-glide-slide>

      <!-- 이동 -->
      <template slot="control">
        <div v-if="numberOfPages > 1" class="tj-hero__control--left" data-glide-dir="<"><i class="fas fa-chevron-left" /></div>
        <div v-if="numberOfPages > 1" class="tj-hero__control--right" data-glide-dir=">"><i class="fas fa-chevron-right" /></div>
      </template>
    </vue-glide>
  </div>
</template>

<script>
import TjButton from "../components/atoms/TjButton.vue";

export default {
  components: {
    TjButton: TjButton,
  },
  data() {
    return {
      glideIndex: 0,
      glideConfig: {
        type: "slider",
        startAt: 0,
        rewind: true,
        autoplay: 5000,
        perView: 1,
        gap: 0,
      },
      heroes: [
        {
          title: "제목1 입니다.",
          subtitle: "부제목1 입니다.",
          buttonText: "버튼",
          image: require("../assets/hero.png"),
          backgroundColor: "transparent",
          href: "https://smartstore.naver.com/taejinshop/products/5415053551",
        },
        {
          title: "제목2 입니다.",
          subtitle: "부제목2 입니다.",
          buttonText: "버튼",
          image: require("../assets/bannerHM.jpg"),
          className: "tj-hero-item--align-right",
          disabled: true,
        },
      ],
    };
  },
  computed: {
    numberOfPages() {
      return this.heroes.length;
    },
    page() {
      if (this.glideIndex) {
        return this.glideIndex + 1;
      }
      return 1;
    },
  },
};
</script>

<style scoped>
.tj-hero {
  position: relative;
}
.tj-hero-item {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  box-sizing: border-box;
  color: #1d1f22;
  display: flex;
  justify-content: flex-start;
  width: 100%;
}
.tj-hero-item__wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  width: 50%; /* 100% */
  min-height: 14rem; /* 14rem */
  padding: 5rem; /* 1rem */
  text-decoration: none;
}
.tj-hero-item__subtitle {
  margin: 0;
  color: #72757e;
  width: 100%; /* 60% */
  font-weight: 400;
  font-size: 0.875rem;
  line-height: 1.4;
  text-transform: uppercase;
}
.tj-hero-item__title {
  margin: 1.5rem 0 2rem 0; /* 0.5rem 0 0 0 */
  color: #1d1f22;
  width: 100%; /* 60% */
  font-weight: 600;
  font-size: 2.625rem; /* 2.25rem */
  line-height: 1.2;
  letter-spacing: 0.1px;
  text-transform: none;
}
.tj-hero-item--align-right {
  justify-content: flex-end;
}
.tj-hero__control--left,
.tj-hero__control--right {
  position: absolute;
  top: 50%;
  transform: translate3d(0, -50%, 0);
  padding: 0 1rem;
}
.tj-hero__control--right {
  right: 0;
}
</style>